import React, { useState, useEffect } from 'react'
import { View, Text, PickerView, PickerViewColumn } from '@tarojs/components'
import dayjs from 'dayjs'
import './index.scss'

interface TimePickerScrollerProps {
  onTimeChange: (date: string, hour: string, minute: string) => void
  defaultValue?: {
    date: number
    hour: number
    minute: number
  }
}

interface TimeSlot {
  date: string
  weekday: string
  fullDate: string
  dateObj: Date
}

const TimePickerScroller: React.FC<TimePickerScrollerProps> = ({ 
  onTimeChange, 
  defaultValue
}) => {
  const [dateList, setDateList] = useState<TimeSlot[]>([])
  const [hourList, setHourList] = useState<string[]>([])
  const [minuteList, setMinuteList] = useState<string[]>([])
  
  const [selectedIndices, setSelectedIndices] = useState([2, 2, 0]) // ‰∏¥Êó∂ÈªòËÆ§ÂÄºÔºå‰ºöÂú®ÂàùÂßãÂåñÂêéÊõ¥Êñ∞

  useEffect(() => {
    initializeLists()
  }, [])

  useEffect(() => {
    // ÂΩìÈÄâÊã©ÊîπÂèòÊó∂ÔºåÈÄöÁü•Áà∂ÁªÑ‰ª∂
    if (dateList.length > 0 && hourList.length > 0 && minuteList.length > 0) {
      const selectedDate = dateList[selectedIndices[0]]
      const selectedHour = hourList[selectedIndices[1]]
      const selectedMinute = minuteList[selectedIndices[2]]
      
      onTimeChange(
        selectedDate.fullDate,
        selectedHour,
        selectedMinute
      )
    }
  }, [selectedIndices, dateList, hourList, minuteList])

  const initializeLists = () => {
    // ÁîüÊàêÊó•ÊúüÂàóË°® (Ââç2Â§© + ‰ªäÂ§© + Âêé2Â§©)
    const dates: TimeSlot[] = []
    const weekdays = ['Âë®Êó•', 'Âë®‰∏Ä', 'Âë®‰∫å', 'Âë®‰∏â', 'Âë®Âõõ', 'Âë®‰∫î', 'Âë®ÂÖ≠']
    
    for (let i = -2; i <= 2; i++) {
      const date = dayjs().add(i, 'day')
      const isToday = i === 0
      
      dates.push({
        date: isToday ? '‰ªäÂ§©' : `${date.month() + 1}Êúà${date.date()}Êó•`,
        weekday: isToday ? '' : weekdays[date.day()],
        fullDate: date.format('YYYY-MM-DD'),
        dateObj: date.toDate()
      })
    }
    setDateList(dates)

    // ÁîüÊàêÂ∞èÊó∂ÂàóË°® (9ÁÇπ-21ÁÇπ)
    const hours: string[] = []
    for (let i = 9; i <= 21; i++) {
      hours.push(`${i}ÁÇπ`)
    }
    setHourList(hours)

    // ÁîüÊàêÂàÜÈíüÂàóË°® (00, 10, 20, 30, 40, 50)
    const minutes: string[] = []
    for (let i = 0; i < 60; i += 10) {
      minutes.push(`${i.toString().padStart(2, '0')}ÂàÜ`)
    }
    setMinuteList(minutes)

    // ËÆ°ÁÆóÈªòËÆ§ÈÄâÊã©ÁöÑÊó∂Èó¥ÔºàÂΩìÂâçÊó∂Èó¥ÁöÑ‰∏ã‰∏Ä‰∏™Êï¥ÁÇπÔºâ
    const now = dayjs()
    const currentHour = now.hour()
    const nextHour = currentHour + 1

    // ÈªòËÆ§ÈÄâÊã©ËÆ°ÁÆó
    let defaultDateIndex = 2 // ‰ªäÂ§©ÁöÑÁ¥¢Âºï
    let defaultHourIndex = 1 // ÈªòËÆ§10ÁÇπÁöÑÁ¥¢Âºï (9ÁÇπÊòØÁ¥¢Âºï0Ôºå10ÁÇπÊòØÁ¥¢Âºï1)
    let defaultMinuteIndex = 0 // ÈªòËÆ§00ÂàÜ

    // Â¶ÇÊûú‰∏ã‰∏Ä‰∏™Êï¥ÁÇπÂú®Ëê•‰∏öÊó∂Èó¥ÂÜÖ (9-21ÁÇπ)
    if (nextHour >= 9 && nextHour <= 21) {
      // Âú®‰ªäÂ§©ÈÄâÊã©‰∏ã‰∏Ä‰∏™Êï¥ÁÇπ
      defaultHourIndex = nextHour - 9 // Â∞èÊó∂Êï∞ÁªÑÁöÑÁ¥¢Âºï (9ÁÇπÂØπÂ∫îÁ¥¢Âºï0)
    } else if (nextHour > 21) {
      // Â¶ÇÊûú‰∏ã‰∏Ä‰∏™Êï¥ÁÇπË∂ÖËøá21ÁÇπÔºåÈÄâÊã©ÊòéÂ§©9ÁÇπ
      defaultDateIndex = 3 // ÊòéÂ§©ÁöÑÁ¥¢Âºï
      defaultHourIndex = 0 // 9ÁÇπÁöÑÁ¥¢Âºï
    } else {
      // Â¶ÇÊûúÂΩìÂâçÊó∂Èó¥Êó©‰∫é9ÁÇπÔºåÈÄâÊã©‰ªäÂ§©9ÁÇπ
      defaultHourIndex = 0 // 9ÁÇπÁöÑÁ¥¢Âºï
    }

    // ‰ΩøÁî®‰º†ÂÖ•ÁöÑdefaultValueÊàñËÆ°ÁÆóÂá∫ÁöÑÈªòËÆ§ÂÄº
    const finalIndices = defaultValue 
      ? [defaultValue.date, defaultValue.hour, defaultValue.minute]
      : [defaultDateIndex, defaultHourIndex, defaultMinuteIndex]

    setSelectedIndices(finalIndices)
  }

  const handleChange = (e: any) => {
    const { value } = e.detail
    setSelectedIndices(value)
  }

  return (
    <View className="time-picker-scroller">
      <PickerView
        indicatorStyle="height: 50px; background-color: rgba(0,0,0,0.05);"
        style={{ width: '100%', height: '200px' }}
        value={selectedIndices}
        onChange={handleChange}
      >
        {/* Êó•ÊúüÂàó */}
        <PickerViewColumn>
          {dateList.map((item, index) => (
            <View key={index} className="picker-item date-item">
              <Text className={`date-text ${item.date === '‰ªäÂ§©' ? 'today' : ''}`}>
                {item.date}
              </Text>
              {item.weekday && (
                <Text className="weekday-text">{item.weekday}</Text>
              )}
            </View>
          ))}
        </PickerViewColumn>

        {/* Â∞èÊó∂Âàó */}
        <PickerViewColumn>
          {hourList.map((hour, index) => (
            <View key={index} className="picker-item hour-item">
              <Text className="hour-text">{hour}</Text>
            </View>
          ))}
        </PickerViewColumn>

        {/* ÂàÜÈíüÂàó */}
        <PickerViewColumn>
          {minuteList.map((minute, index) => (
            <View key={index} className="picker-item minute-item">
              <Text className="minute-text">{minute}</Text>
            </View>
          ))}
        </PickerViewColumn>
      </PickerView>
    </View>
  )
}

export default TimePickerScroller