import React, { useState, useEffect } from 'react'
import Taro, { useRouter } from '@tarojs/taro'
import { View, Text, Image } from '@tarojs/components'
import { AtButton } from 'taro-ui'
import { storeService } from '@/services/store'
import TimePickerScroller from './components/TimePickerScroller'
import type { Store } from '@/types'
import './index.scss'


const StoreAppointmentPage: React.FC = () => {
  const router = useRouter()
  const { id } = router.params
  const [store, setStore] = useState<Store | null>(null)
  const [selectedDate, setSelectedDate] = useState('')
  const [selectedHour, setSelectedHour] = useState('')
  const [selectedMinute, setSelectedMinute] = useState('')
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    loadStoreData()
  }, [id])

  const loadStoreData = async () => {
    try {
      const storeData = await storeService.getStoreDetail(id!)
      setStore(storeData)
    } catch (error) {
      Taro.showToast({
        title: 'Âä†ËΩΩÂ§±Ë¥•',
        icon: 'none'
      })
    } finally {
      setLoading(false)
    }
  }

  const handleTimeChange = (date: string, hour: string, minute: string) => {
    setSelectedDate(date)
    setSelectedHour(hour)
    setSelectedMinute(minute)
  }

  const handleCallStore = () => {
    if (store?.phone) {
      Taro.makePhoneCall({
        phoneNumber: store.phone
      })
    }
  }

  const handleShowLocation = () => {
    if (store?.location) {
      Taro.openLocation({
        latitude: store.location.latitude,
        longitude: store.location.longitude,
        name: store.name,
        address: store.address
      })
    }
  }

  const handleSubmit = () => {
    if (!selectedHour || !selectedMinute) {
      Taro.showToast({
        title: 'ËØ∑ÈÄâÊã©È¢ÑÁ∫¶Êó∂Èó¥',
        icon: 'none'
      })
      return
    }

    // Ë∑≥ËΩ¨Âà∞ÁóáÁä∂ÈÄâÊã©È°µÈù¢
    const timeString = `${selectedHour.replace('ÁÇπ', '')}:${selectedMinute.replace('ÂàÜ', '')}`
    Taro.navigateTo({
      url: `/pages/booking/symptoms/index?type=store&id=${id}&date=${selectedDate}&time=${timeString}`
    })
  }

  const getFormattedDateTime = () => {
    if (!selectedDate || !selectedHour || !selectedMinute) {
      return 'ËØ∑ÈÄâÊã©Êó∂Èó¥'
    }
    
    const dateText = selectedDate === new Date().toISOString().split('T')[0] ? '‰ªäÂ§©' : selectedDate
    const hourText = selectedHour.replace('ÁÇπ', '')
    const minuteText = selectedMinute.replace('ÂàÜ', '')
    
    return `${dateText} ${hourText}:${minuteText}`
  }

  if (!store) return null

  return (
    <View className="store-appointment-page">
      {/* Èó®Â∫ó‰ø°ÊÅØÂ§¥ÈÉ® */}
      <View className="store-header">
        <Image 
          className="store-image" 
          src={store.images[0]} 
          mode="aspectFill"
        />
      </View>

      {/* Èó®Â∫óÂü∫Êú¨‰ø°ÊÅØ */}
      <View className="store-info">
        <View className="info-header">
          <View className="store-details">
            <View className="name-row">
              <Text className="store-name">{store.name}</Text>
              <Text className="distance">{store.distance || 9.0}km</Text>
            </View>
            <View className="hours-row">
              <Text className="business-hours">
                {store.businessHours.start}-{store.businessHours.end}
              </Text>
              <View className={`status ${store.status}`}>
                {store.status === 'normal' && 'Â∞±Ëøë'}
                {store.status === 'busy' && 'ÁπÅÂøô'}
                {store.status === 'full' && 'ÁàÜÊª°'}
              </View>
            </View>
            <Text className="address">{store.address} (ÁîµÂΩ±Èô¢Èó®Âè£)</Text>
          </View>
          <View className="action-buttons">
            <View className="action-btn" onClick={handleCallStore}>
              üìû
            </View>
            <View className="action-btn" onClick={handleShowLocation}>
              üìç
            </View>
          </View>
        </View>
      </View>

      {/* È¢ÑÁ∫¶Êó∂Èó¥ÈÄâÊã© */}
      <View className="appointment-section">
        <Text className="section-title">È¢ÑÁ∫¶Êó∂Èó¥</Text>
        
        {/* Áâπ‰ª∑Êó∂ÊÆµÊèêÁ§∫ */}
        <View className="promotion-tips">
          <View className="tip-item main">
            <Text className="discount">9.5Êäò</Text>
            <View className="tip-content">
              <Text className="tip-title">ÊèêÂâç30ÂàÜÈíü</Text>
              <Text className="tip-desc">10:00ÂºÄÂßã</Text>
            </View>
          </View>
          <View className="tip-item">
            <Text className="discount">9.0Êäò</Text>
            <Text className="tip-desc">ÈîôÂ≥∞È¢ÑÁ∫¶</Text>
            <Text className="tip-desc">ÁÇπÂáªÂâçÂæÄ</Text>
          </View>
        </View>

        {/* ‰∏âÂàóÊó∂Èó¥ÈÄâÊã©Âô® */}
        <TimePickerScroller onTimeChange={handleTimeChange} />
      </View>

      {/* Â∫ïÈÉ®È¢ÑÁ∫¶Ê†è */}
      <View className="bottom-bar">
        <Text className="selected-time">
          È¢ÑÁ∫¶Êó∂Èó¥: {getFormattedDateTime()}
        </Text>
        <AtButton 
          className="submit-btn"
          type="primary"
          onClick={handleSubmit}
          disabled={!selectedHour || !selectedMinute}
        >
          ÈÄâÁóáÁä∂
        </AtButton>
      </View>
    </View>
  )
}

export default StoreAppointmentPage